* {
    font-family: 'Roboto', sans-serif;
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

html {
    font-size: 16px;
}

button {
    cursor: pointer;
    border: 0;
    background: 0;
}

input {
    border: none;
    cursor: pointer;
    font-weight: normal;
}

input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}